  color: #fdfdfd;
  gap: 3.3rem;
  padding: 1.4rem 2.4rem;
  border-radius: 3.2rem;
}
.balloon__btn [class\$=svg-box] {
  width: 2.4rem;
}

.balloon__schedule {
  color: rgba(18, 35, 26, 0.5);
}

[class*=ground-pane],
[class*=events-pane] {
  filter: grayscale(0.5);
}

.widget__map {
  border-radius: 3rem !important;
  overflow: hidden !important;
  --color-greay: #fc5b31;
  --border-radius: 2.4rem;
  height: 45rem !important;
}
@media (max-width: 48em) {
  .widget__map {
    height: 40rem !important;
  }
}

[class*=copyrights-pane] {
  display: none;
}

[class*=controls-pane] {
  display: none;
}

[class*=copyrights-promo] {
  display: none;
}

[class\$=balloon__content] {
  padding: 0 !important;
  width: 40.7rem !important;
  margin-right: 0 !important;
  box-shadow: 2.1rem 1.8rem 6.1rem 0 rgba(173, 173, 173, 0.25) !important;
}

[class\$=balloon__content] > ymaps {
  width: 40.7rem !important;
  height: -webkit-fit-content !important;
  height: -moz-fit-content !important;
  height: fit-content !important;
}

[class\$=balloon-overlay] > ymaps > ymaps {
  box-shadow: none !important;
  top: -30.5rem !important;
  left: -5rem !important;
  border-radius: 1.6rem !important;
  overflow: hidden;
}
@media (max-width: 48em) {
  [class\$=balloon-overlay] > ymaps > ymaps {
    border-radius: 3.2rem !important;
    top: -45.5rem !important;
    left: -12rem !important;
  }
}

[class\$=balloon-overlay] [class\$=balloon__tail] {
  display: none;
}

[class\$=balloon-overlay] [class\$=close-button] {
  margin-right: 0.3rem;
}

.widget__title {
  display: none;
}

.widget__list {
  height: 33rem !important;
  overflow: hidden;
  margin-top: 1.5rem;
}
@media (max-width: 48em) {
  .widget__list {
    position: static;
    margin-top: 1.5rem;
    max-height: 75rem !important;
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
    overflow: hidden;
    transform: translateY(0);
    width: 100%;
  }
}

.widget__content {
  max-width: none;
  padding-right: 0;
  margin-bottom: 1.5rem;
}

.widget__wrapper {
  display: flex;
  overflow: hidden;
  flex-direction: column;
  height: -webkit-fit-content !important;
  height: -moz-fit-content !important;
  height: fit-content !important;
}
@media (max-width: 48em) {
  .widget__wrapper {
    width: 61rem;
  }
}

.ymaps-2-1-79-suggest-item:not(:last-child),
.widget__popup-item:not(:last-child),
.ymaps-2-1-79-suggest-item:last-child,
.widget__popup-item:last-child {
  margin: 0 1.5rem !important;
}

.widget__label-text {
  font-size: 1.6rem;
  line-height: 1.7rem;
}
@media (max-width: 48em) {
  .widget__label-text {
    line-height: 3.4rem;
    font-size: 3.2rem;
  }
}

.widget__header {
  padding: 1.5rem 1.6rem 1.6rem;
  border-radius: 2.4rem;
}
@media (max-width: 48em) {
  .widget__header {
    position: static;
    box-shadow: none;
    width: 100%;
    border-bottom-right-radius: 24px;
    border-bottom-left-radius: 24px;
  }
}

.widget__input {
  border-radius: 1.6rem;
  border: 1px solid transparent;
  padding: 1.55rem 1.6rem;
  transition: border 0.3s;
}

.widget__input--search:not(:focus),
.ydw-suggest-inactive {
  background-color: #f5f4f2 !important;
  border: 1px solid transparent !important;
  transition: border 0.3s;
}

.widget__locations {
  max-height: 75rem;
  border-radius: 2.4rem;
}

.widget__input--search.active,
.widget__location-title,
.widget__location-detail,
[class\$=search__suggest-item] {
  font-size: 1.6rem !important;
  line-height: 1.7rem;
}
@media (max-width: 48em) {
  .widget__input--search.active,
  .widget__location-title,
  .widget__location-detail,
  [class\$=search__suggest-item] {
    line-height: 3.4rem;
    font-size: 3.2rem !important;
  }
}

[class\$=search__suggest-item] {
  padding-top: 2.1rem !important;
  padding-bottom: 2.1rem !important;
}

.widget__popup-item {
  margin-left: 1.5rem !important;
  margin-right: 1.5rem !important;
}

.widget__location-desc,
.widget__location-detail-span {
  font-size: 1.3rem;
  line-height: 1.4rem;
}
@media (max-width: 48em) {
  .widget__location-desc,
  .widget__location-detail-span {
    line-height: 2.8rem;
    font-size: 2.6rem;
  }
}

.widget__input.active {
  border: 1px solid #0e1c15;
}

.widget__label-button {
  width: 2.4rem;
  height: 2.4rem;
}

.widget__label-img,
.widget__location-faq-img {
  width: 2.4rem;
  height: 2.4rem;
}

.widget__location-checkbox-input ~ .widget__location-checkbox-label:before {
  width: 3.2rem;
  height: 3.2rem;
}

.product-reviews__header-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 6rem;
}
.product-reviews__btn {
  background-color: #c6ef4e;
}
.product-reviews__btn svg {
  fill: #0e1c15;
}
.product-reviews__reviews-list {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}
.product-reviews__review-item {
  cursor: pointer;
  background-color: #f6f6f6;
  border-radius: 4rem;
  padding: 4rem 48.2rem 4rem 4rem;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  justify-content: space-between;
  width: 136rem;
}
.product-reviews__review-item-title {
  position: relative;
  z-index: 2;
}
.product-reviews__review-item-title span::before {
  height: 3.6rem;
}
.product-reviews__review-item-text {
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Максимальное количество строк */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.product-reviews__review-item-footer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.product-reviews__review-item-footer-info {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}
.product-reviews__review-item-photo-box {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  overflow: hidden;
}
.product-reviews__review-item-photo-box img {
  object-fit: contain;
}
.product-reviews__button {
  margin-top: 2.4rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.product-reviews__button::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.1rem;
  margin-top: 0.2rem;
  background-color: #0e1c15;
  transition: width 0.3s;
}
.product-reviews__button:hover:after {
  width: 0%;
}

@media (max-width: 48em) {
  .product-reviews__header-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 2.4rem;
    margin-bottom: 3.2rem;
  }
  .product-reviews__btn {
    width: 100%;
  }
  .product-reviews__review-item {
    padding: 4rem;
    width: 100%;
  }
  .product-reviews__review-item-title {
    position: relative;
    z-index: 2;
  }
  .product-reviews__review-item-title span::before {
    height: 3.6rem;
  }
  .product-reviews__review-item-text {
    -webkit-line-clamp: 3;
  }
  .product-reviews__review-item-footer {
    gap: 2.4rem;
    flex-direction: column;
    align-items: flex-start;
  }
  .product-reviews__review-item-footer-info {
    gap: 1.6rem;
  }
  .product-reviews__review-item-photo-box {
    width: 8rem;
    height: 8rem;
  }
  .product-reviews__button:hover:after {
    width: 100%;
  }
}
.watswpp__btn-link {
  z-index: 10;
  position: fixed;
  bottom: 8rem;
  right: 4rem;
}
@media (max-width: 48em) {
  .watswpp__btn-link {
    display: none;
  }
